:root {
    --primary: #A41623;
    --secondary: #F85E00;
    --hover: #FFB563;
}

html
{
    -ms-overflow-style: none; /* Hide scrollbar for IE and Edge */
    scrollbar-width: none; 

    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;

    user-drag: none;  
   user-select: none;
   -moz-user-select: none;
   -webkit-user-drag: none;
   -webkit-user-select: none;
   -ms-user-select: none;

   background-color: var(--secondary);
}

/* Hide scrollbar for Chrome, Safari and Opera */
html::-webkit-scrollbar
{
    display: none;
}

body
{
    margin-bottom: 60px;
    margin-top: 60px;
    margin-left: 0px;
    margin-right: 0px;
}

h1
{
    color: var(--primary);
    text-decoration: underline;
    text-align: center;
    margin:0;
}

p
{
    color: var(--primary);
    margin:0;
    font-size: large;
}

img
{
    
   user-select: none;
   -moz-user-select: none;
   -webkit-user-drag: none;
   -webkit-user-select: none;
   -ms-user-select: none;
}